<template>
	<view  class="contrain">
		<!-- @scrolltoupper='changeNews' -->
		<!-- <scroll-view scroll-y="true"  @scrolltolower="scrollNews" class="content"> -->
			<view class="informs" v-for="(p,i) in news" :key='"p"+i'>
				<view class="itimes">{{p.times}}</view>
				<view class="person-item">
					<view class="ittles">{{p.subject}}</view>
					<view class="iwords">{{p.content}}</view>
					<view class="ifroms">新闻来源：{{p.newsSource}}</view>
				</view>
			</view>
		<!-- </scroll-view>				 -->
	</view>
</template>

<script>
		import date from '../../utils/date.js'
	export default {
		data() {
			return {
				news: [],
				pages: 1,
				pageNum: 1
			}
		},
		onLoad() {
			this.getNews()
		},
		methods: {
			getNews() {
				// uni.showLoading()
				let api = "/message/news";
				this.http(api, "GET", {
					pageNum: this.pageNum,
					pageSize: 6
				}).then(res => {
					// uni.hideLoading()
					uni.stopPullDownRefresh();
					console.log(res)
					if (res.code == 200) {
						let news = res.data.rows;
						news.forEach((item, i) => { 
							item.times = date.toDate(item.createTime)
						})
						this.pages = res.data.pages;
						this.news = this.news.concat(news)
					}
				})
			},
		},
		// 监听用户上拉加载
		onReachBottom(){
			if (this.pages > this.pageNum) {
				this.pageNum += 1
				this.getNews();
			}
		},
		// 监听用户下拉刷新
		onPullDownRefresh(){
			this.pageNum = 1;
			this.news = [];
			this.getNews();
		},
	}
</script>

<style scoped>
	.informs {
		width: 100%;
	}
	
	.itimes {
		color: #FFFFFF;
		font-size: 28rpx;
		width: 100%;
		text-align: center;
	}

	.ittles {
		width: 690rpx;
		height: 56rpx;
		color: #fff;
		font-size: 28rpx;
		padding-left: 12rpx;
		box-sizing: border-box;
		line-height: 56rpx;
		background: #014B84;
		margin: 12rpx 14rpx 0;
	}

	.iwords {
		width: 661rpx;
		white-space: pre-wrap;
		margin-left: 27rpx;
		margin-top: 23rpx;
		color: #fff;
		font-size: 24rpx;
	}

	.ifroms {
		color: #fff;
		font-size: 20rpx;
		margin-top: 29rpx;
		margin-bottom: 29rpx;
		margin-left: 27rpx;
	}

	.person-item {
		overflow: hidden;
	}
</style>
